.main {
    margin-bottom: 6em;
    height:  90vh;
    display: grid;
    grid-template-columns: repeat(6,1fr);;
    gap:20px;
  }
  .cards {
    box-shadow: var(--shadow);
    z-index: 1;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease 0s;
    border: 2px solid #2d2c2c;
    background-color: #79d392;
  }
  .main > div:hover {
    background-color:#2d2c2c;
    overflow: hidden;
    transform: scale(1.08);
    box-shadow: rgba(0, 0, 0, 0.36) 0px 10px 36px 0px,
      rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    border: 5px solid #79d392;
  }
  .main > div > svg {
    
    font-size: 10vh;
    color:#79d392,
  }
  .main > div > h5 {
    position: relative;
    top: 8px;
    color: #79d392;
    background-color: #2d2c2c;
  }
  
  @media (max-width: 800px) {
    .main {
      height: 200vh;
      justify-content: center;
      align-self: center;
    }
    .main > div {
      width: 40%;
      margin-right: 5%;
      margin-bottom: 2%;
      height: 18vh;
    }
  }